.main-navigation-list {
  position: relative;
}

.main-navigation-list__item {
  display: inline-block;
}

.main-navigation-list__anchor {
  color: #000;
  padding: 0 5px;
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: underline;
  @media screen and (min-width: 320px)  {
    text-decoration: none;
  }
}
#search,
#profiles,
#menu  {
  width: 15px;
  @media screen and (min-width: 320px) {
    position: absolute;
    top: 20%;
  }

}

#menu {
  @media screen and (min-width: 320px) {
    left: 55px;
  }
}

#search {
  @media screen and (min-width: 320px) {
    background:  url('/images/icons/search.svg') no-repeat 0 0 / auto 100%;
    text-indent: -99em;
    left: 30px;
  }
}

#profiles {
  @media screen and (min-width: 320px) {
    background:  url('/images/icons/user.svg') no-repeat 0 0 / auto 100%;
    text-indent: -99em;
  }
}
